* {
  box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  line-height: 100%;
}

body {
  margin: 0;
  overflow-x: hidden;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 40px 30px;
  padding-top: 30px;
  padding-bottom: 155px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
    padding-bottom: 197px;
  }
}

.people .person {
  margin: -10px -10px -46px;
  padding: 10px 10px 46px;
  transition: transform 1s;
}

.people .person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 10px;
  transform: scale(1.2);
}

.people .person__photo {
  position: relative;
}

.people .person__img {
  border-radius: 50%;
  width: 100%;
  height: auto;
  transition: border-radius 1s;
  display: block;
}

.people .person:hover .person__img {
  border-radius: 25%;
}

.people .person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 42%;
  right: 5%;
}

.people .person__details {
  margin-top: 8px;
}

.people .person__name {
  color: #2f4f4f;
  text-align: center;
  font-size: 20px;
  display: block;
}

.people .person__extra {
  opacity: 0;
  color: #000;
  text-align: center;
  width: 100%;
  margin-left: -10px;
  padding-top: 9px;
  font-size: 18px;
  font-weight: 400;
  position: absolute;
}

.people .person:hover .person__extra {
  opacity: 1;
}

.people .person__sport:after {
  content: ",";
}

.people .person__pay:before {
  content: "$";
}

.people .person__pay:after {
  content: "M";
}
